import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ManageUserComponent } from './manage-user/manage-user.component';
import { BlockchainExplorerComponent } from './blockchain-explorer/blockchain-explorer.component';
import { DocumentHistoryComponent } from './document-history/document-history.component';
import { ManageFaqsComponent } from './manage-faqs/manage-faqs.component';
import { AuthGuard } from 'src/app/auth.guard';

const routes: Routes = [
  {
    path: '',
    children: [
      { path: '', redirectTo: 'login', pathMatch: 'full' },
      {
        path: 'login',
        component: LoginComponent,
      },
      {
        path: 'dashboard',
        component: DashboardComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'manage-user',
        component: ManageUserComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'block-chain',
        component: BlockchainExplorerComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'document-history',
        component: DocumentHistoryComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'manage-faqs',
        component: ManageFaqsComponent,
        canActivate: [AuthGuard]
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
